* {
  box-sizing: border-box;
}

body {
  --hover-product-name: #34568b;
  --blue-accent: #00acdc;
  --main-accent: #060b35;
  --secondary: #616070;
  --white: #fff;
  --gray-elements: #f3f3f3;
  --column-count: 1;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 400;
}

h2, p {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 1024px;
  margin: 0 auto;
  display: flex;
}

.header__logo {
  height: 40px;
  margin-left: 50px;
}

.header__nav {
  margin-right: 50px;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  display: flex;
}

.header__nav-item {
  list-style: none;
  position: relative;
}

.header__nav-item:not(:last-child) {
  margin-right: 20px;
}

.header__nav-link {
  color: var(--main-accent);
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.header__nav-link:hover {
  color: var(--blue-accent);
}

@media (width >= 1200px) {
  .header {
    width: 1200px;
  }
}

.is-active {
  color: var(--blue-accent);
}

.is-active:after {
  content: "";
  background: var(--blue-accent);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.container {
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  align-items: center;
  column-gap: 48px;
  width: 1024px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --column-count: 4;
  }
}

@media (width >= 1200px) {
  .container {
    width: 1200px;
  }
}

.card {
  border: 1px solid var(--gray-elements);
  text-align: center;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  margin-bottom: 46px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card__picture {
  justify-content: center;
  margin: 0 0 40px;
  display: flex;
}

.card__picture-img {
  width: 160px;
  height: 134px;
}

.card__product {
  text-align: start;
  margin-bottom: 16px;
}

.card__product-name {
  color: var(--main-accent);
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: var(--secondary);
  font-size: 10px;
  line-height: 14px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__product-name {
  color: var(--hover-product-name);
}

.card__raiting {
  text-align: center;
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__raiting-reviewes {
  color: var(--main-accent);
  font-size: 10px;
  line-height: 16px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  padding: 0;
  display: flex;
}

.card__price-name {
  color: var(--secondary);
  font-size: 12px;
  line-height: 18px;
}

.card__price-price {
  color: var(--main-accent);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__link-buy {
  background-color: var(--blue-accent);
  border: 1px solid var(--blue-accent);
  text-transform: uppercase;
  color: var(--white);
  border-radius: 5px;
  width: 166px;
  font-size: 14px;
  font-weight: 700;
  line-height: 38px;
  text-decoration: none;
  transition: all .3s;
}

.card__link-buy:hover {
  background-color: var(--white);
  color: var(--blue-accent);
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  display: inline-block;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

